.centered {
    padding-top: 1em;
    display: flex;
    justify-content: center;
    text-align: center;
}

.items {
    display: flex;
    flex-direction: column;

    max-width: 70vw;
}

.items-list {
    padding-bottom: 1em;
    border-bottom: 1px solid gray;
}

.text-input {
    padding: 1em;
    margin: 0.5em;
    border-radius: 0.7em;
    width: 25em;
}