
.App {
  background-color: #141a32;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  color: white;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#main-header {
  display: flex;
  border-bottom: 2px solid #5a6583;
}

#main-header img {
  min-height: 50px;
  height: 5vh;
  margin-top: auto;
  margin-bottom: auto;
}

#shopping-cart {
  height: 50px;
  width: 50px;

  position: absolute;
  top: 2vh;
  right: 3vw;

  user-select: none;
  -moz-user-select: none;
}

#shopping-cart-btn {
  display: inline-block;
  height: 100%;
  width: 100%;
}

.centered {
  padding-top: 1em;
  display: flex;
  justify-content: center;
  text-align: center;
}

.products {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-evenly;

  max-width: 70vw;
}

.product-card {
  background-color: #5a6583;
  padding: 10px;
  border-radius: 1em;

  max-width: 20vw;
  min-width: 200px;

  margin-top: 35px;

  box-shadow: #0c0f1d 3px 10px 5px;
}
.product-title {
  color: #809cf5;
}
/*
#141a32 // dark

#3e65fb // blue
#d2d2d2 // greyish white
#809cf5 // light blue
#5a6583 // blueish dark grey
*/
.product-price {
  color: #d2d2d2;
}

.product-description {
  margin: auto;
  width: 60%;
  color: white;
}

.product-bottom {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.misc-button {
  border: none;
  background: #3e65fb;
  color: white;
  text-align: center;
  text-decoration: none;
  display: block;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}

.cart-button {
  margin: auto;
  padding: 6px 10px 6px 10px;
  box-shadow: #141a32 2px 3px 2px;
}

.cart-button:hover {
  background-color: #6394f8;
}


.cart-item-btn {
  border: none;
  color: white;
  background-color: #3e65fb;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9em;
}

.cart-item-add {
  margin-right: 4px;
}

.cart-item-sub {
  margin-left: 4px;
}

.item-quantity {
  color: white;
  font-weight: bold;
}

#main-footer {
  margin-top: 50px;
  border-top: 2px solid #5a6583;
  min-height: 100px;
}

/* ------------------------------------------- */



*, *:before, *:after {
  box-sizing: border-box;
}

.lighter-text {
  color: #d2d2d2;
}
.main-color-text {
  color: #809cf5;
}

.shopping-cart {
  margin: 20px 0;
  float: right;
  background: #5a6583;
  width: 320px;
  position: relative;
  border-radius: 3px;
  padding: 20px;
  box-shadow: #141a32 -5px 10px 5px;
}
.shopping-cart .shopping-cart-header {
  padding-bottom: 28px;
}
.shopping-cart .shopping-cart-header .shopping-cart-total {
  float: right;
}
.shopping-cart .shopping-cart-items {
  padding-left: 0px;
}
.shopping-cart .shopping-cart-items li {
  margin-bottom: 10px;
}
.shopping-cart .shopping-cart-items img {
  float: left;
  margin-right: 12px;
}
.shopping-cart .shopping-cart-items .item-name {
  display: block;
  padding-top: 10px;
  font-size: 16px;
  color: #141a32;
}
.shopping-cart .shopping-cart-items .item-price {
  color: #6394f8;
  margin-right: 8px;
}

.shopping-cart .shopping-cart-items .item-quantity-container {
  color: #d2d2d2;
  display: inline;
  font-size: 1.1em;
}

.cart-item {
  list-style-type: none;
}

.checkout-button {
  background: #3e65fb;
  color: white;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  display: block;
  border-radius: 3px;
  font-size: 16px;
  margin: 25px 0 15px 0;
}
.checkout-button:hover {
  background: #809cf5;
}
.cart-item:after {
  content: "";
  display: table;
  clear: both;
}
